          <div id="sidebarMenu">
            <ul class="sidebarMenuInner">
              <li>
                <span @click="goToCourse">Home</span>
              </li>
              <li v-if="isAdmin">
                <span @click="unChecked"
                  ><router-link :to="{ name: 'VideoAdmin' }"
                    >Video Admin</router-link
                  ></span
                >
              </li>
              <li v-if="isAdmin">
                <span @click="unChecked">
                  <router-link :to="{ name: 'ToolkitAdmin' }"
                    >Toolkit Admin</router-link
